/* logo */
h1.logo {
    position: absolute;
    left: 47%;
}

.logo a {
    font-size: 46px;
    color: #00BFA5;
    font-family: 'Niconne', cursive;
    letter-spacing: 1px;
}

.logo i {
    position: absolute;
    font-size: 26px;
    transform: rotate(-71deg);
    bottom: 30px;
