    font-size: 70px;
    color: #d6d6d6;
    transition: 0.5s all;
    -webkit-transition: 0.5s all;
    -moz-transition: 0.5s all;
    -ms-transition: 0.5s all;
    -o-transition: 0.5s all;
}

.parts-w3pvt i:hover {
    color: #FF5722;
    transition: 0.5s all;
    -webkit-transition: 0.5s all;
    -moz-transition: 0.5s all;
    -ms-transition: 0.5s all;
    -o-transition: 0.5s all;
}

/* //partners */

/* stats */
.stats-info {
    background: url() no-repeat center;
    background-size: cover;
    -webkit-background-size: cover;
    -o-background-size: cover;
    -moz-background-size: cover;
    -ms-background-size: cover;
}

h4.stat-text-wthree {
    max-width: 600px;
    font-size: 40px;
    font-family: 'Niconne', cursive;
    font-weight: 500;
    letter-spacing: 1px;
}

.stats-grid-w3-agile span {
    font-size: 30px;
    background: #00BFA5;
    width: 65px;
    height: 65px;
    line-height: 2.1;
    border-radius: 5px;
    text-align: center;
    color: #fff;
}

.counter {
    font-size: 40px;
    line-height: 1.2;
}

/* //stats */

/* team */
.caption {
    text-align: center;
}

.team-text h4 {
    font-size: 26px;
    color: #000;
    letter-spacing: 1px;
    margin: 1.2em 0 .8em;
    font-weight: 600;
}

.caption ul li {
    display: inline-block;
}

.caption ul li:nth-child(2) {
    margin: 0 .3em;
}

.caption ul li a i {
    color: #fff;
    width: 35px;
    height: 35px;
    font-size: 16px;
    line-height: 2.2;
}

.caption ul li a i:hover {
    transform: rotateY(360deg);
    transition: .5s all;
}

.caption ul li a i.f1 {
    background: #3b5998;
}

.caption ul li a i.f2 {
    background: #00aced;
}

.caption ul li a i.f3 {
    background: #F44336;
}

/* //team */

/* footer */
footer {
    background: url() no-repeat bottom;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    -moz-background-size: cover;
}

p.footer-para {
    max-width: 650px;
    font-size: 15px;
}

/* footer logo */
.logo2 {
    position: relative;
}

.logo2 a {
    font-size: 50px;
    color: #FF5722;
    font-family: 'Niconne', cursive;
    letter-spacing: 1px;
}

.logo2 i {
    font-size: 26px;
    bottom: 31px;
    left: 43.69%;
    color: #FF5722;
    position: absolute;
    transform: rotate(-71deg);
}

/* //footer logo */

/* social icons */
.footercopy-social ul li,
.contact-left-footer ul li {
    display: inline-block;
}

.footercopy-social ul li a {
    color: #333;
    text-align: center;
}

.footercopy-social ul li a span.fa-facebook-f {
    background: #3b5998;
}

.footercopy-social ul li a span:hover {
    opacity: 0.8;
}

.footercopy-social ul li a span.fa-twitter {
    background: #1da1f2;
}

.footercopy-social ul li a span.fa-rss {
    background: #f26522;
}

.footercopy-social ul li a span.fa-vk {
    background: #45668e;
}

.footercopy-social ul li a span {
    height: 38px;
    width: 38px;
    font-size: 15px;
    line-height: 2.5;
    color: #fff;
    border-radius: 50%;
    transition: 0.5s all;
    -webkit-transition: 0.5s all;
    -moz-transition: 0.5s all;
    -o-transition: 0.5s all;
    -ms-transition: 0.5s all;
}

/* //social icons */

/* address */
.contact-left-footer ul li p i {
    color: #101010;
}

.contact-left-footer ul li p a,
.contact-left-footer ul li p {
    color: #807d7d;
}

/* //address */

/* copyright */
.w3l-copy p {
    letter-spacing: 2px;
}

.w3l-copy p a {
    color: #FF5722;
}

.w3l-copy p a:hover {
    color: #000;
}

/* //copyright */
